import { RouteConfig } from "../types/routeConfig"; //  RouteConfig 
import { ROUTES } from "../constants/routes"; // 
import HomePage from "../pages/home/homePage";
import AboutPage from "../pages/aboutPage";
import AnimatorPage from "../pages/animator/animatorPage";
import ProtectedPage from "../pages/protectedPage"; // 
import EatWhat from "../pages/eatWhatSpinner/eatWhat";
import StockInfoPage from "../pages/stockInfo/stockInfoPage";

// 
const routes: RouteConfig[] = [
  { path: ROUTES.HOME, name: "", component: HomePage, showInNavbar: true },
  {
    path: ROUTES.ABOUT,
    name: "",
    component: AboutPage,
    showInNavbar: true,
  },
  {
    path: ROUTES.ANIMATOR,
    name: "",
    component: AnimatorPage,
    showInNavbar: true,
  },
  {
    path: ROUTES.EATWHAT,
    name: "",
    component: EatWhat,
    showInNavbar: true,
  },
  {
    path: ROUTES.STOCK_INFO,
    name: "",
    component: StockInfoPage,
    showInNavbar: true,
  },
  {
    path: ROUTES.PROTECTED,
    name: "",
    component: ProtectedPage,
    protected: true, // 
    showInNavbar: true,
  },
];

export default routes;
