import React, { useRef, useEffect, useState } from "react";
import { Food } from "../types";

type Props = {
  foods: Food[];
};

const SpinnerCanvas: React.FC<Props> = ({ foods = [] }) => {
  // ======  ======
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [isSpinning, setIsSpinning] = useState(false); // 
  const [selectedFood, setSelectedFood] = useState<string | null>(null); // 
  const [hoverAddress, setHoverAddress] = useState<string | null>(null); // 
  const [tooltipPosition, setTooltipPosition] = useState<{
    x: number;
    y: number;
  } | null>(null); // Tooltip 

  // ======  ======
  // 
  const [size, setSize] = useState(300);
  const center = size / 2;
  const numSegments = foods.length;

  //  hover 
  const textBounds = useRef<
    { x: number; y: number; width: number; height: number; index: number }[]
  >([]);

  // ======  ======
  useEffect(() => {
    drawWheel(0); // 
  }, [foods, size]);

  // ======  size ======
  useEffect(() => {
    const handleResize = () => {
      let screenWidth;
      if (window.innerWidth > 1200) {
        screenWidth = window.innerWidth * 0.35;
      } else if (window.innerWidth > 769) {
        screenWidth = window.innerWidth * 0.425;
      } else {
        screenWidth = window.innerWidth * 0.85;
      }
      setSize(screenWidth);
    };

    handleResize(); // 
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  // ======  hover  ======
  useEffect(() => {
    setSelectedFood(null); // 
    const canvas = canvasRef.current;
    if (!canvas) return;

    const handleMouseMove = (e: MouseEvent) => {
      const rect = canvas.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;

      // 
      const hover = textBounds.current.find(
        (b) => x >= b.x && x <= b.x + b.width && y >= b.y && y <= b.y + b.height
      );

      if (hover) {
        setHoverAddress(foods[hover.index].address ?? null);
        setTooltipPosition({ x: e.clientX, y: e.clientY });
      } else {
        setHoverAddress(null);
        setTooltipPosition(null);
      }
    };

    canvas.addEventListener("mousemove", handleMouseMove);
    return () => {
      canvas.removeEventListener("mousemove", handleMouseMove);
    };
  }, [foods]);

  // ======  ======
  const drawWheel = (angleInDegrees: number) => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    ctx.clearRect(0, 0, size, size);
    ctx.save();
    ctx.translate(center, center);
    ctx.rotate((angleInDegrees * Math.PI) / 180); // 

    const startAngle = -Math.PI / 2; // 012
    textBounds.current = [];

    for (let i = 0; i < numSegments; i++) {
      const segmentAngle = (2 * Math.PI) / numSegments;
      const foodStartAngle = startAngle + i * segmentAngle;

      // 
      ctx.beginPath();
      ctx.moveTo(0, 0);
      ctx.arc(0, 0, center - 10, foodStartAngle, foodStartAngle + segmentAngle);
      ctx.fillStyle = `hsl(${(i * 360) / numSegments}, 80%, 70%)`;
      ctx.fill();
      ctx.stroke();

      // 
      ctx.save();
      ctx.rotate(foodStartAngle + segmentAngle / 2);
      ctx.translate(center - 60, 0);
      ctx.rotate(Math.PI / 2);
      ctx.fillStyle = "#000";
      ctx.font = "14px sans-serif";
      const text = foods[i].name;
      const textWidth = ctx.measureText(text).width;
      ctx.fillText(text, -textWidth / 2, 0);

      //  hover 
      const x =
        center +
        Math.cos(foodStartAngle + segmentAngle / 2) * (center - 60) -
        textWidth / 2;
      const y =
        center +
        Math.sin(foodStartAngle + segmentAngle / 2) * (center - 60) -
        7;
      textBounds.current.push({ x, y, width: textWidth, height: 14, index: i });

      ctx.restore();
    }

    ctx.restore();

    // 
    ctx.beginPath();
    ctx.moveTo(center, 10);
    ctx.lineTo(center - 10, 30);
    ctx.lineTo(center + 10, 30);
    ctx.closePath();
    ctx.fillStyle = "red";
    ctx.fill();
  };

  // ======  ======
  const spin = () => {
    if (isSpinning) return; // 

    setIsSpinning(true);
    setSelectedFood(null);
    setHoverAddress(null);

    const duration = 3000; // 
    const start = performance.now();
    const totalRotation = 10 * 360 + Math.random() * 360; // 10 + 

    const animate = (now: number) => {
      const elapsed = now - start;
      const progress = Math.min(elapsed / duration, 1); //  0~1
      const easing = 1 - Math.pow(1 - progress, 3); // easeOutCubic 

      const currentAngle = totalRotation * easing;
      drawWheel(currentAngle);

      if (progress < 1) {
        requestAnimationFrame(animate); // 
      } else {
        // ======  ======

        const normalizedAngle = totalRotation % 360; // 
        const anglePerSegment = 360 / numSegments;
        const shiftedAngle = (360 - normalizedAngle) % 360; // 
        const selectedIndex =
          Math.floor(shiftedAngle / anglePerSegment) % numSegments;

        setSelectedFood(foods[selectedIndex].name); // 
        setIsSpinning(false);
      }
    };

    requestAnimationFrame(animate);
  };

  // ======  ======
  return (
    <div className="container-fluid p-0">
      <div className="row">
        <div className="col-12">
          <h1 className="text-3xl font-bold"></h1>
        </div>

        <div className="col-12 position-relative text-center">
          <canvas
            ref={canvasRef}
            width={size}
            height={size}
            className="border rounded-full"
          />

          {hoverAddress && tooltipPosition && (
            <div
              style={{
                position: "fixed",
                top: tooltipPosition.y + 10,
                left: tooltipPosition.x + 10,
                background: "rgba(0,0,0,0.75)",
                color: "#fff",
                padding: "4px 8px",
                borderRadius: "4px",
                pointerEvents: "none",
                fontSize: "0.8rem",
                zIndex: 9999,
              }}
            >
              {hoverAddress}
            </div>
          )}
        </div>

        <div className="col-12 mt-3">
          <button
            onClick={spin}
            disabled={isSpinning}
            className={`px-6 py-2 text-white rounded 
            ${isSpinning ? "bg-secondary" : "bg-info"}`}
          >
            {isSpinning ? "..." : ""}
          </button>
        </div>

        {selectedFood && (
          <div className="col-12 mt-2 text-xl text-green-700 fw-bold">
             {selectedFood}
          </div>
        )}
      </div>
    </div>
  );
};

export default SpinnerCanvas;
