/* eslint-disable prettier/prettier */
import React, { useEffect, useState } from "react";
import SpinnerCanvas from "./components/spinnerCanvas";
import { Coordinates, Food } from "./types";
import { addFoodData, deleteFoodData, getFoodData } from "./api/foodApi";
import { printValue } from "../../utils/createElement";
import SlotMachine from "./components/slotMachine";
import CardFlip from "./components/cardFlip";
import LoadingOverlay from "../../components/common/loadingOverlay";

// ====== Tab  ======
const groupLabels = ["1", "2", "3", "4"];
const modeLabels = ["", "", ""];

const EatWhatSpinner: React.FC = () => {
  // ======  ======
  const [foods, setFoods] = useState<Food[]>([]); // 
  const [activeGroupIndex, setActiveGroupIndex] = useState<number>(0); //  index
  const [newFoodName, setNewFoodName] = useState<string>(""); // 
  const [newFoodAddress, setNewFoodAddress] = useState<string>(""); // 
  const [baseAddress, setBaseAddress] = useState<string>(""); // 
  const [baseLatLng, setBaseLatLng] = useState<Coordinates | null>({
    lat: 24.1397996,
    lng: 120.6486454,
  }); // 
  const [isLoading, setIsLoading] = useState(false);
  const [mode, setMode] = useState<string>("");

  // 
  useEffect(() => {
    const loadFoods = async () => {
      setIsLoading(true); // 

      try {
        const data = await getFoodData(activeGroupIndex); // 

        if (baseLatLng) {
          const updatedData = await updateCurrentTabFoodDistances(
            data,
            baseLatLng
          );
          setFoods(updatedData);
        } else {
          setFoods(data);
        }
      } catch (error) {
        console.error("Error loading food data:", error);
      } finally {
        setIsLoading(false); // 
      }
    };

    loadFoods();
  }, [activeGroupIndex, baseLatLng]);

  // ======  ======
  const handleAddFood = async () => {
    const trimmedName = newFoodName.trim();
    const trimmedAddress = newFoodAddress.trim();
    if (!trimmedName) return;

    // 
    if (foods.some((f) => f.name === trimmedName)) {
      // 
      alert("");
      return; // 
    }

    setIsLoading(true);

    try {
      //  undefined
      let coordinates = undefined;

      // 
      if (trimmedAddress) {
        const latLng = await getCoordinatesFromAddress(trimmedAddress);
        if (latLng) {
          coordinates = latLng;
        }
      }

      const newFood = {
        name: trimmedName,
        address: trimmedAddress || undefined,
        coordinates: coordinates, //  undefined
        group: `${activeGroupIndex}`,
      };

      //  API
      const addResponse = await addFoodData(newFood, activeGroupIndex);

      //post API, 
      setTimeout(async () => {
        const updatedFoods = await getFoodData(activeGroupIndex); // 

        // 
        if (updatedFoods) {
          if (baseLatLng) {
            const foodsWithDistance = await updateCurrentTabFoodDistances(
              updatedFoods,
              baseLatLng
            );
            setFoods(foodsWithDistance);
          } else {
            setFoods(updatedFoods);
          }
        }
      }, 100); // 

      // 
      setNewFoodName("");
      setNewFoodAddress("");
    } catch (error) {
      console.error("Error adding food:", error);
      // alert("");
    } finally {
      setIsLoading(false); //  loading 
    }
  };

  // ======  ======
  const handleDeleteFood = async (foodId: number) => {
    const confirmDelete = window.confirm("");
    if (!confirmDelete) return;

    setIsLoading(true);

    try {
      //  API foodId 
      const deleteResponse = await deleteFoodData(foodId, activeGroupIndex);

      //post API, 
      setTimeout(async () => {
        const updatedFoods = await getFoodData(activeGroupIndex); // 

        // 
        if (updatedFoods) {
          setFoods(updatedFoods);
        }
      }, 100); // 
    } catch (error) {
      console.error("Error deleting food:", error);
      alert("");
    } finally {
      setIsLoading(false); //  loading 
    }
  };

  // ======  ======
  const handleGroupChange = (index: number) => {
    if (index >= foods.length) {
      // 
      setFoods([]);
    }
    setActiveGroupIndex(index);
  };

  // 
  const handleGetCurrentLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          //  baseAddress  baseLatLng
          setBaseAddress("");
          setBaseLatLng({ lat: latitude, lng: longitude });
        },
        (error) => {
          alert("");
        }
      );
    } else {
      console.error("");
    }
  };

  // 
  const getCoordinatesFromAddress = async (
    address: string
  ): Promise<{ lat: number; lng: number } | null> => {
    if (!address.trim()) return null;

    setIsLoading(true);
    const latLng = await getLatLngFromAddress(address);
    setTimeout(() => {
      setIsLoading(false);
    }, 100); // 

    return latLng || null;
  };

  //  Nominatim API 
  //  null
  async function getLatLngFromAddress(
    address: string
  ): Promise<{ lat: number; lng: number } | null> {
    try {
      const response = await fetch(
        `https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(address)}&limit=1`,
        {
          headers: {
            "User-Agent": "EatWhatSpinner/1.0 (your.email@example.com)",
          },
        }
      );
      if (!response.ok) return null;
      const data = await response.json();
      if (data.length > 0) {
        return {
          lat: parseFloat(data[0].lat),
          lng: parseFloat(data[0].lon),
        };
      }
      return null;
    } catch (error) {
      console.error(":", error);
      return null;
    }
  }

  // Haversine
  // 
  function calculateDistance(
    lat1: number,
    lng1: number,
    lat2: number,
    lng2: number
  ): number {
    const R = 6371; //  (km)
    const dLat = (lat2 - lat1) * (Math.PI / 180);
    const dLng = (lng2 - lng1) * (Math.PI / 180);
    const a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(lat1 * (Math.PI / 180)) *
        Math.cos(lat2 * (Math.PI / 180)) *
        Math.sin(dLng / 2) *
        Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c;
  }

  // 
  const calculateFoodDistance = (
    coordinates: { lat: number; lng: number },
    baseLatLng: { lat: number; lng: number }
  ): number | undefined => {
    if (!coordinates) return undefined;

    const distance = calculateDistance(
      baseLatLng.lat,
      baseLatLng.lng,
      coordinates.lat,
      coordinates.lng
    );
    return Math.round(distance * 10) / 10; // 
  };

  // 
  const updateCurrentTabFoodDistances = async (
    foods: Food[],
    baseLatLng: Coordinates
  ) => {
    if (!baseLatLng) return foods; //  baseLatLng foods

    // 
    const updatedFoods: Food[] = (foods ?? []).map((food) => {
      if (food.coordinates) {
        // 
        const distance = calculateFoodDistance(food.coordinates, baseLatLng);
        return { ...food, distance };
      }
      // 
      return { ...food, distance: undefined };
    });

    return updatedFoods; //  setFoods 
  };

  // baseLatLng 
  useEffect(() => {
    const updateDistances = async () => {
      if (baseLatLng) {
        const updated = await updateCurrentTabFoodDistances(foods, baseLatLng);
        setFoods(updated);
      }
    };

    updateDistances();
  }, [baseLatLng]);

  // ======  ======
  return (
    <div className="container-fluid p-0">
      {/* Loading Spinner Overlay */}
      <LoadingOverlay isLoading={isLoading} />
      <div className="row">
        {/*  */}
        <div className="col-md-6 col-sm-12 d-flex flex-column align-items-center my-3">
          <div className="row">
            <div className="col-12">
              {/* ======  Tab  ====== */}
              <div className="btn-group mb-3">
                {modeLabels.map((label, index) => (
                  <button
                    key={index}
                    className={`btn btn-outline-primary ${mode === label ? "active" : ""}`}
                    onClick={() => setMode(label)}
                  >
                    {label}
                  </button>
                ))}
              </div>
            </div>
          </div>
          {mode === "" && <SpinnerCanvas foods={foods} />}
          {mode === "" && <SlotMachine foods={foods} />}
          {mode === "" && <CardFlip foods={foods} />}
        </div>

        {/*  */}
        <div className="col-md-6 col-sm-12 d-flex flex-column align-items-center my-3 px-5">
          {/* ======  Tab  ====== */}
          <div className="btn-group mb-3">
            {groupLabels.map((label, index) => (
              <button
                key={index}
                className={`btn btn-outline-primary ${activeGroupIndex === index ? "active" : ""}`}
                onClick={() => handleGroupChange(index)}
              >
                {label}
              </button>
            ))}
          </div>

          {/*  */}
          <div className="input-group mb-2">
            <input
              type="text"
              value={baseAddress}
              onChange={(e) => setBaseAddress(e.target.value)}
              placeholder=""
              className="form-control"
            />
            <button
              className="btn btn-primary"
              onClick={async () => {
                const latLng = await getCoordinatesFromAddress(baseAddress);
                if (latLng) {
                  setBaseLatLng(latLng);
                }
              }}
              disabled={isLoading || !baseAddress}
            >
              {isLoading ? "..." : ""}
            </button>
            <button
              onClick={handleGetCurrentLocation}
              className="btn btn-warning"
            >
              
            </button>
          </div>

          <h4 className="mb-3"> - {groupLabels[activeGroupIndex]}</h4>

          {/*  */}
          <div className="input-group mb-2">
            <input
              type="text"
              value={newFoodName}
              onChange={(e) => setNewFoodName(e.target.value)}
              placeholder=""
              className="form-control"
            />
          </div>

          <div className="input-group mb-2">
            <input
              type="text"
              value={newFoodAddress}
              onChange={(e) => setNewFoodAddress(e.target.value)}
              placeholder=""
              className="form-control"
            />
          </div>

          <button
            className="btn btn-success mb-3"
            onClick={handleAddFood}
            disabled={isLoading || !newFoodName}
          >
            {isLoading ? "..." : ""}
          </button>

          {/*  */}
          {foods.length === 0 ? (
            <p className="text-muted fst-italic"></p>
          ) : (
            <ul className="list-group text-start w-100">
              {(foods ?? []).map((food, index) => (
                <li
                  key={index}
                  className="list-group-item d-flex justify-content-between align-items-center"
                >
                  <div>
                    <div>{food.name}</div>
                    {food.address && (
                      <small className="text-muted">{food.address}</small>
                    )}
                    {food.distance !== undefined && (
                      <div className="text-primary">
                        {food.distance} km
                      </div>
                    )}
                  </div>
                  <button
                    className="btn btn-sm btn-outline-danger"
                    onClick={() => handleDeleteFood(food.id)}
                    disabled={isLoading}
                  >
                    
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
      {/* <div className="text-start">
        {printValue(foods)}
      </div> */}
    </div>
  );
};

export default EatWhatSpinner;
